// Basic
@import "./variables";
@import "./helpers";
// Plugins
@import '~react-agenda/build/styles.css';
@import '~react-datetime/css/react-datetime.css';
@import 'plugins/toast.css';
@import 'plugins/table.css';

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: $secondary-font-family;
}

html, body {
  background: #000 !important;
  font-family: $secondary-font-family !important;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

::-webkit-scrollbar-track {
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: #efecec;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.dashboard-screen{
  height: 100vh;
    width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .swipe{
    position: fixed;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .review{
    position: fixed;
    top: 75%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.save-button {
  background: rgba(255, 255, 255, 0.2) !important;
  backdrop-filter: blur(10px) !important;
  border: none !important;
  transition: all 0.3s ease-in-out !important;
  color: rgba(255, 255, 255, 0.5) !important;
  position: absolute !important;
  top: 20px !important;
  right: 20px !important;
}

.like-button{
  background: rgba(255, 255, 255, 0.2) !important;
  backdrop-filter: blur(10px) !important;
  border: none !important;
  height: 75px !important;
  transition: all 0.3s ease-in-out !important;

  &.is-green:hover{
    background: rgba(27, 155, 23, 0.15) !important;
  }

  &.is-red:hover{
    background: rgba(139, 21, 21, 0.15) !important;
  }

  .keyboard-key{
    box-shadow: 1px 2px 0px 2px #bcbcbc !important;
  }
}

.overlay-footer{
  position: fixed;
  bottom: 10px;
  width: 100vw;
}


.PlayerRSWP{
 // display: none;
}

.play-button-overlay{
  position: absolute;
  top: 0px;
  right: 0;
  bottom: 0;
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0,0,0,0.3);
  backdrop-filter: blur(30px);
  cursor: pointer;
}

.card-song{
  background: rgba(255,255,255,0.5);
  backdrop-filter: blur(30px);
}
